.wrapper {
	margin-block: 40px;

	:global {
		.ant-upload.ant-upload-select {
			width: 100%;
		}
		.ant-upload-list {
			bottom: -20px;
			position: absolute;
			text-overflow: ellipsis;
			width: 150px;
		}
	}

	.rowPlace {
		& > *:not(:last-child) {
			position: relative;
			&::after {
				bottom: 0;
				right: 0;
				content: "";
				position: absolute;
				display: inline-block;
				width: 1px;
				height: 40px;
				background: var(--sPrimary);
			}
		}
	}
}
