.card {
	.circleApt {
		justify-content: center !important;
		color: #fff !important;
	}
	p {
		margin-bottom: 0;
	}

	.numLabel {
		display: flex;
		align-items: center;
		width: fit-content;
		gap: 5px;
		border-radius: 16px;
		background: #f8f9fe;

		p {
			padding-inline: 5px 16px;
			justify-self: center;
			font-weight: 600;
			font-size: 14px;
			letter-spacing: 1.12px;
		}

		span {
			font-weight: 700;
			border-radius: 50%;
			width: 24px;
			height: 24px;
			background: #e8e9f1;
		}
	}
}
